<template>
  <div class="voting-wrapper">
    <div class="voting-container">
      <div class="header">
        {{ questionData.question }}
      </div>
      <div class="subheader" v-if="questionData.type === 1">
        Можно отметить только один пункт
      </div>
      <div class="subheader" v-if="questionData.type === 2">
        Можно отметить несколько пунктов
      </div>
      <div
        v-if="!isVoted && questionData.type === 1"
        class="answers-container"
      >
        <div class="answer-item"
          v-for="answer in questionData.variants" 
          :key="answer.id"
          @click="answerClick(answer.id)"
          :class="{'background-blue': selectedAnswerId===answer.id}"
        >
          <div class="answer-item-text">
            {{ answer.name }}
          </div>
        </div>
      </div>

      <div
        v-if="!isVoted && questionData.type === 2"
        class="answers-container"
      >
        <div class="answer-item"
          v-for="answer in questionData.variants" 
          :key="answer.id"
          @click="answerClick(answer.id)"
          :class="{'background-blue': selectedAnswerIds.includes(answer.id)}"
        >
          <div class="answer-item-text">
            {{ answer.name }}
          </div>
        </div>
      </div>

      <div
        v-if="isVoted"
        v-html="'Спасибо!<br>Ваш голос учтен'"
        class="is-voted-text"
      >
      </div>

      <div class="voting-button-container">
        <div
          v-if="!isVoted"
          @click="vote"
          :class="{'enable': selectedAnswerId || selectedAnswerIds.length > 0}"
          class="voting-button"
        >
          ПРОГОЛОСОВАТЬ
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      serverURL: "https://dev1.med-game.ru/api/v1",
      questionData: {},
      selectedAnswerId: null,
      isVoted: false,
      selectedAnswerIds: [],
    }
  },
  computed: {
    
  },
  methods: {
    getQuestionData(){
      axios({
        method: 'get',
        url: `${this.serverURL}/voting/${this.$route.params.sessionId}`,
      })
      .then((questionData) => {
        // console.log(questionData);
        this.questionData = questionData.data.data
      })
      .catch((error) => {
        console.log('Ошибка:');
        console.log(error)
        return error
      })
    },
    answerClick(id){
      if(this.questionData.type === 2) {
        const index = this.selectedAnswerIds.indexOf(id);
        if (index === -1) {
          console.log("push id: " + id)
          this.selectedAnswerIds.push(id);
        } else {
          this.selectedAnswerIds.splice(index, 1);
        }
      } else {
        if (this.selectedAnswerId === id) {
          this.selectedAnswerId = null
        }
        else {
          this.selectedAnswerId = id
        }
      }
    },
    vote(){
      console.log('Vote')
      if(this.questionData.type === 2) {
        console.log('Vote type2' + this.questionData.type)
        console.log("this.$route.params.sessionId: " + this.$route.params.sessionId)
        for(let i = 0; i < this.selectedAnswerIds.length; i++){
          console.log('this.selectedAnswerIds[i]: ' + this.selectedAnswerIds[i])
          axios({
            url: `${this.serverURL}/voting/${this.$route.params.sessionId}/answer`,
            method: 'post',
            data: {
              // "variant_ids": this.selectedAnswerIds
              "variant_id": this.selectedAnswerIds[i]
            }
          })
        
          .then(() => {
            this.isVoted = true
            // localStorage.setItem("isVoted", 1)
            localStorage.setItem(this.$route.params.sessionId, 1)          
          })
          .catch(function (error) {
            console.log(error);
          })
        }  
      } else {
      axios({
          url: `${this.serverURL}/voting/${this.$route.params.sessionId}/answer`,
          method: 'post',
          data: {
            "variant_id": this.selectedAnswerId
          }
        })
        .then(() => {
          this.isVoted = true
          // localStorage.setItem("isVoted", 1)
          localStorage.setItem(this.$route.params.sessionId, 1)          
        })
        .catch(function (error) {
          console.log(error);
        })    
      }
  }
  },
  mounted() {
    this.getQuestionData()
    const value = localStorage.getItem(this.$route.params.sessionId)
    if (value == 1) {
      this.isVoted = true
    }
    else {
      this.isVoted = false
    }
  },
}
</script>

